import React from 'react';
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  styled,
  tableCellClasses,
} from '@mui/material';
import { useAxios } from '../../hooks';
import { getBookList } from '../../api/books';
import { convertDate } from '../../utils';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.secondary.dark,
    color: theme.palette.common.white,
    fontWeight: '600',
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },

  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

export const Statistics = () => {
  const { data: bookList, error, loading } = useAxios(getBookList);

  return (
    bookList && (
      <TableContainer component={Paper}>
        <Table>
          <TableHead sx={{ fontWeight: '800' }}>
            <TableRow>
              <StyledTableCell component="th">Id</StyledTableCell>
              <StyledTableCell>Title</StyledTableCell>
              <StyledTableCell>Publish date</StyledTableCell>
              <StyledTableCell>Page count</StyledTableCell>
              <StyledTableCell>Description</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {bookList.map((book) => (
              <StyledTableRow key={book.id}>
                <StyledTableCell>{book.id}</StyledTableCell>
                <StyledTableCell>{book.title}</StyledTableCell>
                <StyledTableCell>
                  {convertDate(book.publishDate)}
                </StyledTableCell>
                <StyledTableCell>{book.pageCount}</StyledTableCell>
                <StyledTableCell>{book.description}</StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    )
  );
};
